import stocks from'../../../Data/data'
const state={
  stocks:[]
};
const mutations={
	'set-stocks'(state,stocks){
		state.stocks=stocks;
	},
	'rnd-stock'(state){
    state.stocks.forEach(stock=>{
      stock.price=Math.round(stock.price*(1+Math.random()-0.5));
    	});
	}
};
const actions={
  buyStocks:({commit},order)=>{
  	commit('buy-stock',order);
  },
  setStocks:({commit})=>{
  	commit('set-stocks',stocks)
  	  },
  	  randomizeStocks:({commit})=>{
  	  	commit('rnd-stock')
  	  }
};
const getters={
	stocks:state=>{
		return	state.stocks;
		
	}
};
export default{
	state,
	actions,
	getters,
	mutations
};


