<template>
  <div class="container">
    <appHeader></appHeader>
    <div class="row">
      <div class="col-xs-12">
        <transition name="slide" mode="out-in">
           <router-view></router-view>
        </transition>
        

      </div>
    </div>
   
  </div>
</template>

<script>
  import appHeader from './components/Header.vue'
export default {
  components:{
    appHeader:appHeader
  },
  created(){
     this.$store.dispatch('setStocks');
  }
 
  
}
</script>

<style>

.slide-enter-active {
  animation: slide-in 300ms ease-out forwards;
}

.slide-leave-active{
 animation:  slide-out 300ms ease-out forwards;
}
@keyframes slide-in{
from{
transform:translateY(-30px);
opacity: 0;
}
to{
transform:translateY(0);
opacity: 1;
}
}
@keyframes slide-out{
from{
transform:translateY(0);
opacity: 1;
}
to{
transform:translateY(-30px);
opacity: 0;
}
}
</style>



