<template>
	<div>
		<appStock v-for="stock in stocks" :stock="stock"></appStock>	
		</div>
	</div>
	
</template>
<script >
	import stock from './stock.vue';
import {mapGetters} from 'vuex';

	export default{
		computed:{
         ...mapGetters({
             stocks:'stockPortfolio'
         })
		},
		components:{
          appStock:stock
		}
	}
</script>


