const state={
  fund:1000,
  stocks:[]
};
const mutations={
	'buy-stock'(state,{stockId,quantity,stockPrice}){
		const record = state.stocks.find(element => element.id==stockId);
		if(record){
			 record.quantity+=quantity
		}
		else{
			state.stocks.push({
			id:stockId,
			quantity: quantity

          });
		}
		state.fund-=stockPrice*quantity;
	},
	'sell-stock'(state,{stockId,quantity,stockPrice}){
		const record=state.stocks.find(element=>element.id==stockId);
		if(record.quantity>quantity){
			record.quantity-=quantity
		}
		else{
			state.stocks.splice(state.stocks.indexOf(record),1);
			
          		}
          		state.fund+=stockPrice*quantity;
	},
	'set-portfolio'(state,portfolio){
		state.fund=portfolio.funds;
		state.stocks=portfolio.stockPortfolio?portfolio.stockPortfolio:[];
	}
};
const actions={
	sellStock:({commit},order)=>{
	commit('sell-stock',order);
	}
};
const getters={
	stockPortfolio(state,getters){
		return state.stocks.map(stock=>{
		const record=getters.stocks.find(element=>element.id==stock.id);	
		return{
		id:stock.id,
		quantity: stock.quantity,
		name:record.name,
		price:record.price
		}
		});
        },
		funds(state){
			return state.fund;
		}
	};
	

export default{
	state,
	actions,
	getters,
	mutations
};


